import * as types from "../actionTypes"
import request from "../request"
import { assert, beginTicketValidation } from "./validationHelpers"

const { host } = window.mflix || "http://localhost:5000"

export function validateTicketFourteen() {
  return async dispatch => {
    dispatch(beginTicketValidation("Fourteen"))
    let response = await getPoolSize()
    let timeAssertion = assert(2500, response.wtimeout)
    if (timeAssertion) {
      return dispatch(validateTicketFourteenSuccess())
    } else {
      return dispatch(
        validateTicketFourteenError(
          new Error("The return from the api was incorrect")
        )
      )
    }
  }
}

export function validateTicketFourteenSuccess() {
  return { type: types.VALIDATE_TICKET_FOURTEEN_SUCCESS }
}

export function validateTicketFourteenError(error) {
  return { type: types.VALIDATE_TICKET_FOURTEEN_ERROR, error }
}

/**
 * Ticket 13 internal functions
 */

const getPoolSize = () => {
  return request(`${host}/api/v1/movies/config-options`, {
    method: "GET",
    mode: "cors",
  })
    .then(res => res)
    .catch(error => error)
}



