import * as types from "../actionTypes"
import request from "../request"
import { assert, beginTicketValidation } from "./validationHelpers"

const { host } = window.mflix || "http://localhost:5000"

export function validateTicketFifteen() {
  return async dispatch => {
    dispatch(beginTicketValidation("Fifteen"))
    let response = await checkMovieByIDError()
    let status = assert(response.status, "fail")
    if (status) {
      return dispatch(validateTicketFifteenSuccess())
    } else {
      return dispatch(
        validateTicketFifteenError(
          new Error(
            "The return from the api was incorrect when providing a bad id to search by"
          )
        )
      )
    }
  }
}

export function validateTicketFifteenSuccess() {
  return { type: types.VALIDATE_TICKET_FIFTEEN_SUCCESS }
}

export function validateTicketFifteenError(error) {
  return { type: types.VALIDATE_TICKET_FIFTEEN_ERROR, error }
}

/**
 * Ticket 15 internal functions
 */

const checkMovieByIDError = () => {
  return request(`${host}/api/v1/movies/id/foobar`, {
    method: "GET",
    mode: "cors",
  })
    .then(res => res)
    .catch(error => error)
}



