import * as types from "../actionTypes"
import request from "../request"
import { beginTicketValidation } from "./validationHelpers"

const { host } = window.mflix || "http://localhost:5000"

const invalid_auth_token =
  "eyJ0eXAiOiJKV1QiLCJhbGciOiJIUzI1NiJ9..q9z_tG7gEqaRMfrbTpj9Jz52vocqOBWgEpCd3KC6giI"

// the martian
const statusOk = response => response.status === "success"
export function validateTicketEleven() {
  return async dispatch => {
    try {
      dispatch(beginTicketValidation("Eleven"))
      let testUser = genRandomUser()
      const registerResponse = await register(testUser)
      const { auth_token } = registerResponse
      const badReportRequest = await getUserReport(invalid_auth_token)
      const goodReportRequest = await getUserReport(auth_token)
      if (statusOk(badReportRequest)) {
        throw new Error("Invalid response to bad user report request")
      }
      if (
        !statusOk(goodReportRequest) ||
        goodReportRequest.report.length !== 20
      ) {
        throw new Error("Invalid response to good user report request")
      }
      deleteUser(auth_token, testUser)
      return dispatch(validateTicketElevenSuccess())
    } catch (e) {
      return dispatch(validateTicketElevenError(new Error(e.message)))
    }
  }
}

export function validateTicketElevenSuccess() {
  return { type: types.VALIDATE_TICKET_ELEVEN_SUCCESS }
}

export function validateTicketElevenError(error) {
  return { type: types.VALIDATE_TICKET_ELEVEN_ERROR, error }
}

/**
 * Ticket 11 internal functions
 */

const getUserReport = token => {
  return request(`${host}/api/v1/user/comment-report`, {
    method: "GET",
    mode: "cors",
    headers: {
      Authorization: `Bearer ${token}`,
      "content-type": "application/json",
    },
  })
    .then(res => res)
    .catch(error => error)
}

const genRandomUser = () => ({
  name: Math.random()
    .toString(36)
    .substr(2, 9),
  email: `${Math.random()
    .toString(36)
    .substr(2, 9)}@${Math.random()
    .toString(36)
    .substr(2, 5)}.${Math.random()
    .toString(36)
    .substr(2, 3)}`,
  password: `${Math.random()
    .toString(36)
    .substr(2, 9)}`,
})

const deleteUser = (token, user) => {
  return request(`${host}/api/v1/user/delete`, {
    method: "DELETE",
    mode: "cors",
    headers: {
      Authorization: `Bearer ${token}`,
      "content-type": "application/json",
    },
    body: JSON.stringify({ password: user.password }),
  })
    .then(res => res)
    .catch(error => error)
}

const register = user => {
  return request(`${host}/api/v1/user/make-admin`, {
    method: "POST",
    mode: "cors",
    headers: {
      "content-type": "application/json",
    },
    body: JSON.stringify(user),
  })
    .then(user => user)
    .catch(error => error)
}



