import * as types from "../actionTypes"
import request from "../request"
import { beginTicketValidation } from "./validationHelpers"

const { host } = window.mflix || "http://localhost:5000"

const invalid_auth_token =
  "eyJ0eXAiOiJKV1QiLCJhbGciOiJIUzI1NiJ9..q9z_tG7gEqaRMfrbTpj9Jz52vocqOBWgEpCd3KC6giI"

// the martian
const movie_id = "573a13eff29313caabdd82f3"
const statusOk = status => status === "success"
export function validateTicketNine() {
  return async dispatch => {
    try {
      dispatch(beginTicketValidation("Nine"))
      let testUser = genRandomUser()
      const registerResponse = await register(testUser)
      const { auth_token } = registerResponse
      let postC, badC, goodC
      const commentResponse = await submitComment(
        movie_id,
        "feefee",
        auth_token
      )
      if (!statusOk(commentResponse.status)) {
        throw new Error("Unable to post a comment")
      } else {
        postC = true
      }
      const firstCommentID = commentResponse.comments[0]._id
      let badResponse = await editComment(
        firstCommentID,
        "fizzlebazzle",
        invalid_auth_token,
        movie_id
      )
      if (badResponse.status === "success") {
        throw new Error("Was able to update a comment that wasn't owned")
      } else {
        badC = true
      }

      let goodUpdate = await editComment(
        firstCommentID,
        "fazzlebizzle",
        auth_token,
        movie_id
      )
      if (!statusOk(goodUpdate.status)) {
        throw new Error("Unable to update comment")
      } else {
        goodC = true
      }

      if (postC && badC && goodC) {
        deleteUser(auth_token, testUser)
        return dispatch(validateTicketNineSuccess())
      } else {
        return dispatch(
          validateTicketNineError(
            new Error("The return from the api was incorrect")
          )
        )
      }
    } catch (e) {
      return dispatch(validateTicketNineError(new Error(e.message)))
    }
  }
}

export function validateTicketNineSuccess() {
  return { type: types.VALIDATE_TICKET_NINE_SUCCESS }
}

export function validateTicketNineError(error) {
  return { type: types.VALIDATE_TICKET_NINE_ERROR, error }
}

/**
 * Ticket 9 internal functions
 */

const genRandomUser = () => ({
  name: Math.random()
    .toString(36)
    .substr(2, 9),
  email: `${Math.random()
    .toString(36)
    .substr(2, 9)}@${Math.random()
    .toString(36)
    .substr(2, 5)}.${Math.random()
    .toString(36)
    .substr(2, 3)}`,
  password: `${Math.random()
    .toString(36)
    .substr(2, 9)}`,
})

const deleteUser = (token, user) => {
  return request(`${host}/api/v1/user/delete`, {
    method: "DELETE",
    mode: "cors",
    headers: {
      Authorization: `Bearer ${token}`,
      "content-type": "application/json",
    },
    body: JSON.stringify({ password: user.password }),
  })
    .then(res => res)
    .catch(error => error)
}

const register = user => {
  return request(`${host}/api/v1/user/register`, {
    method: "POST",
    mode: "cors",
    headers: {
      "content-type": "application/json",
    },
    body: JSON.stringify(user),
  })
    .then(user => user)
    .catch(error => error)
}

function submitComment(movieID, comment, token) {
  return request(`${host}/api/v1/movies/comment`, {
    method: "POST",
    mode: "cors",
    headers: {
      Authorization: `Bearer ${token}`,
      "content-type": "application/json",
    },
    body: JSON.stringify({
      movie_id: movieID,
      comment,
    }),
  })
    .then(json => json)
    .catch(e => e)
}

function editComment(commentID, update, token, movie_id) {
  return request(`${host}/api/v1/movies/comment`, {
    method: "PUT",
    mode: "cors",
    headers: {
      Authorization: `Bearer ${token}`,
      "content-type": "application/json",
    },
    body: JSON.stringify({
      comment_id: commentID,
      updated_comment: update,
      movie_id,
    }),
  })
    .then(json => json)
    .catch(e => e)
}



