import * as types from "../actionTypes";
import request from "../request";
import { beginTicketValidation } from "./validationHelpers";

const { host } = window.mflix || "http://localhost:5000";

export function validateTicketSixteen() {
  return async dispatch => {
    dispatch(beginTicketValidation("Sixteen"));
    let response = await getUserInfo();
    let dbAssertion = response.db === "mflix";
    let roleAssertion = response.role === "readWrite";
    if (dbAssertion && roleAssertion) {
      return dispatch(validateTicketSixteenSuccess());
    } else {
      return dispatch(
        validateTicketSixteenError(
          new Error(
            "It doesn't appear you have configured the application user"
          )
        )
      );
    }
  };
}

export function validateTicketSixteenSuccess() {
  return { type: types.VALIDATE_TICKET_SIXTEEN_SUCCESS };
}

export function validateTicketSixteenError(error) {
  return { type: types.VALIDATE_TICKET_SIXTEEN_ERROR, error };
}

/**
 * Ticket 13 internal functions
 */

const getUserInfo = () => {
  return request(`${host}/api/v1/movies/config-options`, {
    method: "GET",
    mode: "cors"
  })
    .then(res => res)
    .catch(error => error);
};



