import * as types from "../actionTypes"
import request from "../request"
import { assert, beginTicketValidation } from "./validationHelpers"

const { host } = window.mflix || "http://localhost:5000"

export function validateTicketOne() {
  return async dispatch => {
    dispatch(beginTicketValidation("One"))
    let response = await getMovies()
    let filtersAssertion = assert(0, Object.keys(response.filters).length)
    let moviesAssertion = assert(20, response.movies.length)
    let resultsAssertion = assert(46014, response.total_results)
    let pageAssertion = assert(0, response.page)
    if (
      [
        filtersAssertion,
        moviesAssertion,
        resultsAssertion,
        pageAssertion,
      ].every(elem => elem)
    ) {
      return dispatch(validateTicketOneSuccess())
    } else {
      return dispatch(
        validateTicketOneError(
          new Error("The return from the api was incorrect")
        )
      )
    }
  }
}

export function validateTicketOneSuccess() {
  return { type: types.VALIDATE_TICKET_ONE_SUCCESS }
}

export function validateTicketOneError(error) {
  return { type: types.VALIDATE_TICKET_ONE_ERROR, error }
}

/**
 * Ticket 1 internal functions
 */

const getMovies = () => {
  return request(`${host}/api/v1/movies/`, {
    method: "GET",
    mode: "cors",
  })
    .then(res => res)
    .catch(error => error)
}



