import * as types from "../actionTypes"
import request from "../request"
import { assert, beginTicketValidation } from "./validationHelpers"

const { host } = window.mflix || "http://localhost:5000"

export function validateTicketThirteen() {
  return async dispatch => {
    dispatch(beginTicketValidation("Thirteen"))
    let response = await getPoolSize()
    let poolAssertion = assert(50, response.pool_size)
    if ([poolAssertion].every(elem => elem)) {
      return dispatch(validateTicketThirteenSuccess())
    } else {
      return dispatch(
        validateTicketThirteenError(
          new Error("The return from the api was incorrect")
        )
      )
    }
  }
}

export function validateTicketThirteenSuccess() {
  return { type: types.VALIDATE_TICKET_THIRTEEN_SUCCESS }
}

export function validateTicketThirteenError(error) {
  return { type: types.VALIDATE_TICKET_THIRTEEN_ERROR, error }
}

/**
 * Ticket 13 internal functions
 */

const getPoolSize = () => {
  return request(`${host}/api/v1/movies/config-options`, {
    method: "GET",
    mode: "cors",
  })
    .then(res => res)
    .catch(error => error)
}



