import request from "../request"
import * as types from "../actionTypes"

const { host } = window.mflix || "http://localhost:5000"

export const searchByQueryAndPage = (which, query, page) => {
  const encodedQuery = encodeURIComponent(query)
  return request(
    `${host}/api/v1/movies/search?${which}=${encodedQuery}&page=${page}`,
    {
      method: "GET",
      mode: "cors",
    }
  )
    .then(res => res)
    .catch(error => error)
}

export const searchByFacetAndPage = (query, page) => {
  const encodedQuery = encodeURIComponent(query)
  return request(
    `${host}/api/v1/movies/facet-search?cast=${encodedQuery}&page=${page}`,
    {
      method: "GET",
      mode: "cors",
    }
  )
    .then(res => res)
    .catch(error => error)
}

export const assert = (expected, actual) => expected === actual

export function beginTicketValidation(ticket) {
  return { type: types.VALIDATING_TICKET, ticket }
}



